import slide from 'components/slide/index'
import http from 'js/http'

export default {
  name: "index",
  components:{
    slide
  },
  data(){
    return{
      noticeInfo:{}
    }
  },
  methods:{
    getData(){
      http.post('/notice/info', {
        id: this.$route.params.id,
      }, (res) => {

        if (res.code !== 0) {
          this.$vux.toast.show(res.msg)
          return
        }
        this.noticeInfo = res.content
        // console.log(this.noticeInfo)
      })
    }
  },
  created(){
    this.getData()
  }
}



